<template>
    <div>
        <v-head></v-head>
        <div class="clear"></div>
        <div class="selectBar" >
          <div id="selectForm">
              <el-form  id="select">
              <div >
              <el-form-item label="课程性质:" class="labels">
              <el-select v-model="typeSelect" placeholder="请选择" class="sel1">
                    <el-option  v-for="item in leixing"  :key="item.value":label="item.label"  :value="item.value">   </el-option>
                </el-select>
              </el-form-item>
              </div>
              <div>
              <el-form-item label="有无余量:" class="labels">
              <el-select v-model="yuliang" placeholder="请选择" class="sel2">
                    <el-option  v-for="item in youwu"  :key="item.value"  :label="item.label"  :value="item.value"> </el-option>
                </el-select>
              </el-form-item>
              <el-button type="primary" @click="search" id="search">查询</el-button>
              </div>
              <div class="clear"> </div>
            </el-form>
          </div>
        </div>
        <div class="tableBar" ref="tableBar">
        <div id="table">
          <el-table
                :data="tableData3"
                id="classTable"
                border>
                <el-table-column
                  
                  prop="cID"
                  label="课程编号"
                  >
                </el-table-column>
                <el-table-column
                  prop="cname"
                  label="课程名字"
               >
                </el-table-column>
                <el-table-column
                  prop="ctype"
                  label="课程性质"
                  >
                </el-table-column>
                <el-table-column
                  prop="classroom"
                  label="上课地点"
                 >
                </el-table-column>
                <el-table-column
                  prop="weeks"
                  label="上课时间"
                  >
                </el-table-column>
                <el-table-column
                  prop="cbelongto"
                  label="开课学院"
                 >
                </el-table-column>
		
                <el-table-column
                  prop="teacher"
                  label="开课教师"
                 >
                </el-table-column>
			
                <el-table-column
                  prop="num"
                  label="课程余量"
                 >
                </el-table-column>
				 <el-table-column
                  label="选否">
		      <template slot-scope="scope">
			   <el-checkbox v-model="scope.row.selected">选择</el-checkbox>
               </template>
                </el-table-column>				
              </el-table>
        
        </div>
	    <br/>
		<el-button type="primary" @click="submit()">提交</el-button>
      </div>
    </div>
</template>

<script>
import vHead from '../commons/StudentNav.vue';

export default {
      created(){
        var vm=this;
        $.ajax({
          type:'post',
          dataType:'json',
          url:'/sutdent/department',
          data:{
            "id":localStorage.getItem("stuId")
          },
          success:function(response){
            if(response!=null){
              vm.depId=response.depID;
              
            }
          },error:function(err){
            console.log(err);
            console.log("error");
          }
        }),
        console.log("")

      },
      data() {
      return {
        depId:'',
        typeSelect: '',
        yuliang:'',
        leixing:[
           {label:"专业选修课",value:2},
           {label:"通选课",value:3},           
        ],
        youwu:[
          {label:"全部",value:0},
          {label:"有",value:1},
          {label:"无",value:2}
        ],
        tableData3: [
          // {
        //   courseid: '123',
        //   coursename: '数据挖掘',
        //   courseclass: '选修课',
        //   adress: 'A1101',
        //   time: '4-18周',
        //   xueyuan: '软件学院',
        //   tname:'XX',
        //   num:'90',
        //   selected:true
        //   }
         ]
      }
    },
    methods:{
      search(){
        console.log(this.yuliang,typeSelect);
        if(this.typeSelect==''){
          this.$message({
            type:'info',
            message:"选课类型不能为空"
          })
        }else{
          var vm=this;
          
          $.ajax({
            type:'post',
            dataType:'json',
            url:'/student/availableCourse',
            data:{
              "depId":this.depId
            },
            success:function(response){
              if(response.string!=""){
                

                vm.$refs.tableBar.style.display="block";
              }else{
                vm.$message({
                  type:'info',
                  message:'暂无此类型选课'
                })
              }
            },
            error:function(err){
              console.log(err);
              console.log("error");
            }
          })


        }

      }
    },

    components:{
        vHead,
    }
}

</script>
 <style scoped>
 .clear{
     clear:both;
 }
.selectBar{
    margin-top:4px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    /* height:80px; */
    padding:10px;
}
.tableBar{
    margin-top:5px;
    padding-top:6px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    /* height:500px; */
    padding:10px;
    /* display:none; */
}
#selectForm{

    padding-top: 1px;

}
#select{
    width:40%;
    margin:0px auto;
}
.labels{
    float:left;
    width:40%;
    display: inline;
}
#search{
  margin-top:36px;
  margin-left:10px;
  float:left;
}
#table{
  width:90%;
  text-align:left;
  margin:0px auto;
}
#classTable{
  width:100%;
}

</style>


